import { Container } from "../../common/Container/styled";
import Header from "../../common/Header";
import Section from "../../common/Section";

const Author = () => (
  <Container>
    <Header title="O autorze" />
    <Section
      title="Radosaw Wrblewski"
      body={
        <>
          <p>
            Mam na imi Radek i mam 41 lat. Pochodz z maej miejscowoci{" "}
            <a
              rel="noreferrer noopener"
              target="_blanc"
              href="https://pl.wikipedia.org/wiki/Kutno"
              title="Otwrz w wikipedii"
            >
              Kutno
            </a>
            , lecej w samym centrum Polski. Moja przygoda z programowaniem
            zacza si ju w ubiegym stuleciu, kiedy to rodzice kupili mi
            komputer{" "}
            <a
              rel="noreferrer noopener"
              target="_blanc"
              href="https://pl.wikipedia.org/wiki/Commodore_64"
              title="Otwrz w wikipedii"
            >
              <i lang="en">Commodore64</i>
            </a>
            . W tym samym okresie dostaem ksik dla dzieci na temat
            programowania w jzyku{" "}
            <a
              rel="noreferrer noopener"
              target="_blanc"
              href="https://pl.wikipedia.org/wiki/BASIC"
              title="Otwrz w wikipedii"
            >
              <i lang="en">BASIC</i>
            </a>
            . I tak to si zaczo. Pniej zainteresowanie programowaniem
            zostao zapomniane, wraz z zamian komputera na rolki.
            <br />
            A do teraz.
            <br />
            Kiedy zobaczyem zaproszenie na live'a dotyczcego programowania
            pomylaem sobie, e moe warto sprbowa. I w ten sposb zacz si
            drugi etap mojej przygody. Mam nadziej, e niedugo bd mg
            wykorzysta nowo zdobyte umiejtnoci na rynku pracy.
          </p>
        </>
      }
    />
  </Container>
);

export default Author;
